<template>
    <div class="bg-gray-800 flex p-8 gap-8 justify-between mt-8
                md:flex-row flex-col border-t border-sky-600">

        <div class="flex flex-col gap-4">
            <h1 class="text-lg">Github</h1>
            <a href="https://github.com/nazililham11"
               class="text-lg font-semibold flex text-gray-100">
                <img alt="Github logo"
                     class="h-8 mr-2 bg-gray-100 rounded-full p-1"
                     :src="'./img/github-logo.png'">
                nazililham11
            </a>
        </div>


        <div class="flex flex-row flex-1 items-start">
            <img src="https://s11.flagcounter.com/count/j1UN/bg_111827/txt_FFFFFF/border_0284C7/columns_2/maxflags_10/viewers_0/labels_1/pageviews_1/flags_0/percent_0/" alt="Flag Counter" border="0"> 

            <div class="flex flex-col text-gray-100 px-4">
                <span v-if="ip !== ''">IP : {{ ip }}</span>
                <span v-if="city !== ''">City : {{ city }}</span>
                <span v-if="org !== ''">Org : {{ org }}</span>
            </div>

        </div>
        



        <div class="flex flex-col gap-4">
            <h1 class="font-semibold text-gray-100 text-lg">
                <Label labels_id="footer__made_with"></Label>
            </h1>
            
            <div class="flex gap-2">
                <img alt="Vue logo"
                     class="h-10"
                     :src="'./img/vue-logo.png'">
                <img alt="Tailwind logo"
                     class="h-10"
                     :src="'./img/tailwind-logo.png'">
            </div>

            <span class="text-gray-100 font-semibold">
                <Label labels_id="footer__visitor"></Label>
                {{ visitor }}
            </span>

        </div>
    </div>
</template>

<script>
import Label from './../components/Label.vue'
import fetchers from './../utils/fetchers'

export default {
    components: { Label },
    data() {
        return {
            visitor: 0,
            ip: "",
            country: "",
            org: ""
        }
    },
    async mounted(){
        this.visitor = await fetchers.visitors()

        const ip = await fetch("https://ipapi.co/json/").then(r => r.json())

        if (ip !== undefined && ip.ip !== ""){
            this.ip = ip.ip === undefined || ip.ip === "" ? "" : ip.ip
            this.city = ip.city === undefined || ip.city === "" ? "" : ip.city
            this.org = ip.org === undefined || ip.org === "" ? "" : ip.org
        }
    }
}

</script>
